.project_details{
    padding: 2em;
}

.project_details h1,
.project_details h2,
.project_details hp{
    margin-bottom: 0.5em;
}
.project_details h1{
    background-color: #222;
    color: #ffbb33;
    padding: 0.4em;
    border-radius: 15px;
}

.project_details span{
    font-weight: bold;
}

.details_container,
.service_from_container{
    border-bottom: 1px solid #7a7a7a;
    margin-bottom: 1.2em;
    padding-bottom: 1.2em;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.btn{
    background-color: #222;
    color: #fff;
    padding: 0.5em 1em;
    text-decoration: none;
    transition: 0.5s;
    border: none;
    border-radius: 1.5em;
    max-height: 45px;    
}
.btn:hover{
    color: #ffbb33;
}

.project_info{
    width: 100%;
}
