.message{
    width: 100%;
    padding: 1em;
    border: 1px solid #000;
    margin-bottom: 2em;
    border-radius: 10px;
    text-align: center;
}

.success{
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb;
}

.error{
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6CB
}

.warning{
    color: #a87102;
    background-color: #f1eadb;
    border-color: #f0dfba
}
