.project_card{
    padding: 1em;
    border: 1px solid #7a7a7a;
    border-radius: 10px;
    width: 23%;
    margin: 0.5em;
}

.project_card h4{
    background-color: #222;
    color: #ffbb33;
    padding: 0.4em;
    margin-bottom: 1.3em;
    font-size: 1.3em;
    border-radius: 10px;
}

.project_card p{
    color: #7a7a7a;
    margin-bottom: 1em;
}

.project_card span{
    font-weight: bold;
}

.category_text{
    display: flex;
    align-items: center;
}

.category_text span{
    display: block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #ccc;
    margin-right: 5px;
}
.category_text .infra{
    background-color: #ffaebc;
}

.category_text .desenvolvimento{
    background-color: #a0e7e5;
}

.category_text .content{
    background-color: #bcf8c8;
}
.category_text .design{
    background-color: #963746;
}
.category_text .planejamento{
    background-color: #da55ad;
}

.project_card_actions{
    margin-top: 1.2em;
    display: flex;
    align-items: center;
}

.project_card_actions a, 
.project_card_actions button{
    text-decoration: none;
    border: none;
    background-color: #222;
    color: #fff;
    font-size: .9em;
    padding: .6em 1em;
    cursor: pointer;
    margin-right: 1em;
    border: 1px solid #222;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.5s;
}

.project_card_actions a:hover, 
.project_card_actions button:hover{
color: #ffbb33;
}

.project_card svg{
    margin-right: 0.5em;
}
